import React,{useState} from "react";
import cube from "../../styles/assets/images/enterprise_blockchain/cube.svg";
import business_svg from "../../styles/assets/images/enterprise_blockchain/business_image.svg";
import boy_look from "../../styles/assets/images/enterprise_blockchain/boy_look.svg";
import embrance from "../../styles/assets/images/enterprise_blockchain/embrance.svg";
import man from "../../styles/assets/images/enterprise_blockchain/justtry_man.svg";
import TalkToUs from "../../components/common/talk-to-us";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";

import Loading from "../../components/common/loading";
import { useForm } from "react-hook-form";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import "animate.css";
const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const EnterpriseBlockchain = (props) => {
  const { db } = props;
  console.log("db::", db);
  const [loading, setLoading] = useState(false);
  const [talkToUsShow, setTalkToUsShow] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleToggleTalkToUs = () => {
    setTalkToUsShow(!talkToUsShow);
    reset();
  };

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
      setTimeout(() => {
        handleToggleTalkToUs();
      }, 1000);
    } catch (e) {
      utils.showErrMsg("Please enter the valid details");
      console.log("e::", e);
      setLoading(false);
    }
  };

  return (
    <div className="enterprise_blockchain_container">
      <div className="enterprise_blockchain_row">
        <section className="enterprise_block_hero_section">
          <div className="enterprise_block_hero_lhs">
            <h1>Enterprise Blockchain Development Company</h1>
            <p>
              Join the ranks of visionary businesses by leveraging our
              state-of-the-art enterprise blockchain solutions and services. At
              Justtry, we don't just promise greatness; we deliver it.
            </p>
            <button className="stock_btn" onClick={handleToggleTalkToUs}>
              TALK TO US
            </button>
          </div>
          <div className="enterprise_block_hero_rhs">
            <img src={cube} alt="cube_image" />
          </div>
        </section>
        <section className="enterprise_block_business enterprise_block_padding">
          <div className="enterprise_business_lhs">
            <h1>
              Blockchain Technology For Enterprises: Igniting Business
              Revolution!
            </h1>
            <div className="enterpries_business_img_wrapper">
              <img src={business_svg} alt="business explaining ui" />
            </div>
          </div>
          <div className="enterprise_business_rhs">
            <p>
              Blockchain technology has truly swept the world off its feet with
              its innovative approach when it comes to secure data storage and
              management. Initially associated with cryptocurrencies, this
              technology has now found its place in various arenas beyond them.
              Businesses all over the globe are adopting it because it allows
              them to create smarter, more efficient, and reliable solutions.
              For instance, smart contracts, the backbone of blockchain, can
              automate tasks ranging from payments to managing supply chains.
              The result? Faster actions and cost savings.{" "}
            </p>
            <p>
              But that's not all! Businesses are also turning to blockchain to
              keep their data safe and cozy. It's like a digital vault where you
              can securely store and share information with your trusted
              partners without ever worrying about privacy or security breaches.
              In 2023, it's not just about adopting technology; it's about
              keeping up with the future. Blockchain is leading the charge, and
              businesses are eager to hop on board to stay ahead of the game.
              Why wait any longer? Now's the perfect moment to integrate
              blockchain technology into your business. Take the first step by
              joining hands with Justtry - a leading enterprise blockchain
              development company, today!
            </p>
          </div>
        </section>
        <section className="enterpries_block_look enterprise_block_padding">
          <div className="enterprise_block_top">
            <h1>Enterprise Blockchain: A Closer Look</h1>
          </div>
          <div className="enterprise_block_center">
            <div className="enterprise_block_look_cells">
              <p>
                Since the launch of Bitcoin in 2009, blockchain technology has
                been hailed by experts for its potential to securely streamline
                business operations. However, there have been certain drawbacks
                associated with public blockchains like Bitcoin that have made
                companies hesitant to fully embrace this technology. For
                instance, businesses handling sensitive financial information
                often require a high level of data confidentiality. This becomes
                challenging on a public blockchain where every node on the
                network can access and view transaction records.
              </p>
            </div>
            <div className="enterprise_block_look_cells">
              <div className="enterprise_look_img_frame">
                <img src={boy_look} alt="boy looking to explain blockchain" />
              </div>
            </div>
            <div className="enterprise_block_look_cells">
              <p>
                Businesses often handle a high volume of transactions and need
                systems that can handle sudden surges efficiently. Traditional
                decentralized public blockchains slow down transaction
                processing because every node must validate transactions. For
                example, Bitcoin, which can process just 5-7 transactions per
                second, isn't suitable for companies handling hundreds or
                thousands of transactions per second. Enterprises, however, have
                found a solution to these challenges in the form of enterprise
                blockchains.
              </p>
            </div>
          </div>
          <div className="enterprise_block_bottom">
            <p>
              Enterprise blockchain is a type of permissioned blockchain network
              designed to cater to the unique needs of businesses. They bring a
              wave of transformative benefits to enterprises, including enhanced
              security, immutability, scalability, and control. In essence, they
              are revolutionizing the way businesses operate, charting a new
              course for success.
            </p>
          </div>
        </section>
        <section className="enterprise_block_cta_section_wrapper enterprise_block_padding">
          <div className="enterprise_block_cta_section">
            <div className="enterprise_block_cta_content">
              <h2>
                Don't miss the opportunity to revolutionize your business with
                the power of blockchain. Get started today!
              </h2>
              <div className="enterprise_block_btn_wrapper">
                <button
                  className="blockchain_cta_btn"
                  onClick={handleToggleTalkToUs}
                >
                  Talk to us
                </button>
              </div>
            </div>
            <div className="enteprise_block_cta"></div>
          </div>
        </section>
        <section className="enterprise_blockchain_service enterprise_block_padding">
          <div className="blockchain_service_header">
            <h1>
              Unlock Business Potential with Our Enterprise Blockchain Services
            </h1>
          </div>
          <div className="blockchain_service_cells">
            <div className="blockchain_service">
              <p>Permissioned Blockchain Development</p>
            </div>
            <div className="blockchain_service">
              <p>
                When it comes to permissioned blockchain development, we've got
                your back, ensuring your transactions are not just safe but
                ultra-secure. Our top-notch solutions come packed with fantastic
                features like permission-based access and lightning-fast
                transactions, just to give you a glimpse of what we offer.
              </p>
            </div>
          </div>
          <div className="blockchain_service_cells">
            <div className="blockchain_service">
              <p>Smart Contract Development</p>
            </div>
            <div className="blockchain_service">
              <p>
                We power your business with a customized smart contract solution
                tailored to your business’s specific needs. These smart
                contracts are designed to make transactions faster, more
                cost-effective, and less prone to errors or fraud. We seamlessly
                deploy the final solution on any compatible blockchain.
              </p>
            </div>
          </div>
          <div className="blockchain_service_cells">
            <div className="blockchain_service">
              <p>Cryptocurrency Exchange Development</p>
            </div>
            <div className="blockchain_service">
              <p>
                Our seasoned blockchain developers craft top-notch
                cryptocurrency exchange software. With features like a
                multi-chain wallet, automated KYC and AML, and a crypto trading
                bot, your platform is bound to succeed. Partner with us for a
                remarkable crypto exchange experience!
              </p>
            </div>
          </div>
          <div className="blockchain_service_cells">
            <div className="blockchain_service">
              <p>Decentralized Applications (dApps) Development</p>
            </div>
            <div className="blockchain_service">
              <p>
                Our expert team specializes in strategizing and developing
                cross-platform dApps, leveraging cutting-edge tools like
                Ethereum Geth and Truffle. These dApps are fully customizable to
                suit a wide range of industries and can be effortlessly
                integrated into your existing business workflows.
              </p>
            </div>
          </div>
          <div className="blockchain_service_cells">
            <div className="blockchain_service">
              <p>Enterprise Blockchain Consulting Services</p>
            </div>
            <div className="blockchain_service">
              <p>
                We provide guidance on harnessing the power of blockchain
                technology to transform your business into a futuristic one. Our
                goal is to help businesses automate their operations, introduce
                transparency to their processes, and cut down on unnecessary
                costs.
              </p>
            </div>
          </div>
          <div className="blockchain_service_cells">
            <div className="blockchain_service">
              <p>Custom-Tailored Solutions Development</p>
            </div>
            <div className="blockchain_service">
              <p>
                Our expertise lies in building bulletproof blockchain solutions
                perfectly tailored to your industry's distinctive requirements.
                With an all-encompassing development process, you can rest
                assured that your solution will always operate at peak
                performance.
              </p>
            </div>
          </div>
          <div className="blockchain_service_cells">
            <div className="blockchain_service">
              <p>Cryptocurrency Wallet Development</p>
            </div>
            <div className="blockchain_service">
              <p>
                Our skilled development team comes equipped with prior
                experience in building multi-cryptocurrency wallets fortified
                with avant-garde security features. We specialize in building
                crypto wallets that facilitate secure transactions through
                robust end-to-end encryption. Your digital assets are in safe
                hands with us!
              </p>
            </div>
          </div>
        </section>
        <section className="enterprise_blockchain_embrance enterprise_block_padding">
          <h1>Why Businesses Should Embrace Enterprise Blockchain?</h1>
          <div className="blockchain_embrance_bottom">
            <div className="blockchain_embrance_lhs">
              <p>
                Enterprise blockchain offers numerous compelling reasons for
                businesses to embrace this transformative technology. One of its
                key strengths is its capacity to provide heightened
                transparency. This fosters trust among business partners and
                stakeholders, an invaluable asset in today's competitive
                landscape. Security is another hallmark of enterprise
                blockchain, fortified by robust cryptographic techniques that
                make data highly resistant to tampering.
              </p>{" "}
              <p>
                Enterprise blockchains eliminate intermediaries and minimize
                manual processes, resulting in significant operational cost
                reductions. Interoperability is another advantage, with some
                enterprise blockchains designed for seamless integration with
                existing systems, facilitating a smoother transition. In
                summary, enterprise blockchain presents a wealth of
                opportunities for businesses, encompassing transparency,
                security, efficiency, cost savings, compliance, and a
                competitive advantage in a rapidly evolving landscape.
              </p>
            </div>
            <div className="blockchain_embrance_rhs">
              <div className="embrance_image_wrapper">
                <img src={embrance} alt="explain about blockchain" />
              </div>
            </div>
          </div>
        </section>
        <section className="enterprise_blockchain_justtry enterprise_block_padding">
          <h1>
            Why Justtry Stands Out as Your Enterprise Blockchain Development
            Partner
          </h1>
          <div className="enterprise_blockchain_dev_wrapper">
            <div className="enterprise_blockchain_dev_lhs">
              <img
                src={man}
                alt="explain about justtry in blockchiain development"
              />
            </div>
            <div className="enterprise_blockchain_dev_rhs">
              <p>
                At Justtry, we believe in a customer-centric approach. We work
                closely with you to understand your goals and challenges and
                then deliver solutions that drive results.  Our team of
                professional developers brings a wealth of experience to the
                table. In a world where change is constant and innovation is
                key, our enterprise blockchain development services are your
                ticket to success. It's time you led the way. Choose us as your
                trusted partner today and embark on a journey of digital
                excellence. Your future starts here! We offer:
              </p>
              <div className="enterprise_blockchain_dev_bottom">
                <div className="blockchain_dev_btns">
                  <div className="blockchain_dev_icon">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="31"
                      height="30"
                      viewBox="0 0 31 30"
                      fill="none"
                    >
                      <g clip-path="url(#clip0_964_1872)">
                        <path
                          fill-rule="evenodd"
                          clip-rule="evenodd"
                          d="M0.5 15C0.5 11.0218 2.08035 7.20644 4.8934 4.3934C7.70644 1.58035 11.5218 0 15.5 0C19.4782 0 23.2936 1.58035 26.1066 4.3934C28.9196 7.20644 30.5 11.0218 30.5 15C30.5 18.9782 28.9196 22.7936 26.1066 25.6066C23.2936 28.4196 19.4782 30 15.5 30C11.5218 30 7.70644 28.4196 4.8934 25.6066C2.08035 22.7936 0.5 18.9782 0.5 15ZM14.644 21.42L23.28 10.624L21.72 9.376L14.356 18.578L9.14 14.232L7.86 15.768L14.644 21.422V21.42Z"
                          fill="#7534FC"
                        />
                      </g>
                      <defs>
                        <clipPath id="clip0_964_1872">
                          <rect
                            width="30"
                            height="30"
                            fill="white"
                            transform="translate(0.5)"
                          />
                        </clipPath>
                      </defs>
                    </svg>
                  </div>
                  <p>Expertise That Matters</p>
                </div>
                <div className="blockchain_dev_btns">
                  <div className="blockchain_dev_icon">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="31"
                      height="30"
                      viewBox="0 0 31 30"
                      fill="none"
                    >
                      <g clip-path="url(#clip0_964_1872)">
                        <path
                          fill-rule="evenodd"
                          clip-rule="evenodd"
                          d="M0.5 15C0.5 11.0218 2.08035 7.20644 4.8934 4.3934C7.70644 1.58035 11.5218 0 15.5 0C19.4782 0 23.2936 1.58035 26.1066 4.3934C28.9196 7.20644 30.5 11.0218 30.5 15C30.5 18.9782 28.9196 22.7936 26.1066 25.6066C23.2936 28.4196 19.4782 30 15.5 30C11.5218 30 7.70644 28.4196 4.8934 25.6066C2.08035 22.7936 0.5 18.9782 0.5 15ZM14.644 21.42L23.28 10.624L21.72 9.376L14.356 18.578L9.14 14.232L7.86 15.768L14.644 21.422V21.42Z"
                          fill="#7534FC"
                        />
                      </g>
                      <defs>
                        <clipPath id="clip0_964_1872">
                          <rect
                            width="30"
                            height="30"
                            fill="white"
                            transform="translate(0.5)"
                          />
                        </clipPath>
                      </defs>
                    </svg>
                  </div>
                  <p>Tailored Solution</p>
                </div>
                <div className="blockchain_dev_btns">
                  <div className="blockchain_dev_icon">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="31"
                      height="30"
                      viewBox="0 0 31 30"
                      fill="none"
                    >
                      <g clip-path="url(#clip0_964_1872)">
                        <path
                          fill-rule="evenodd"
                          clip-rule="evenodd"
                          d="M0.5 15C0.5 11.0218 2.08035 7.20644 4.8934 4.3934C7.70644 1.58035 11.5218 0 15.5 0C19.4782 0 23.2936 1.58035 26.1066 4.3934C28.9196 7.20644 30.5 11.0218 30.5 15C30.5 18.9782 28.9196 22.7936 26.1066 25.6066C23.2936 28.4196 19.4782 30 15.5 30C11.5218 30 7.70644 28.4196 4.8934 25.6066C2.08035 22.7936 0.5 18.9782 0.5 15ZM14.644 21.42L23.28 10.624L21.72 9.376L14.356 18.578L9.14 14.232L7.86 15.768L14.644 21.422V21.42Z"
                          fill="#7534FC"
                        />
                      </g>
                      <defs>
                        <clipPath id="clip0_964_1872">
                          <rect
                            width="30"
                            height="30"
                            fill="white"
                            transform="translate(0.5)"
                          />
                        </clipPath>
                      </defs>
                    </svg>
                  </div>
                  <p>24/7 Support</p>
                </div>
                <div className="blockchain_dev_btns">
                  <div className="blockchain_dev_icon">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="31"
                      height="30"
                      viewBox="0 0 31 30"
                      fill="none"
                    >
                      <g clip-path="url(#clip0_964_1872)">
                        <path
                          fill-rule="evenodd"
                          clip-rule="evenodd"
                          d="M0.5 15C0.5 11.0218 2.08035 7.20644 4.8934 4.3934C7.70644 1.58035 11.5218 0 15.5 0C19.4782 0 23.2936 1.58035 26.1066 4.3934C28.9196 7.20644 30.5 11.0218 30.5 15C30.5 18.9782 28.9196 22.7936 26.1066 25.6066C23.2936 28.4196 19.4782 30 15.5 30C11.5218 30 7.70644 28.4196 4.8934 25.6066C2.08035 22.7936 0.5 18.9782 0.5 15ZM14.644 21.42L23.28 10.624L21.72 9.376L14.356 18.578L9.14 14.232L7.86 15.768L14.644 21.422V21.42Z"
                          fill="#7534FC"
                        />
                      </g>
                      <defs>
                        <clipPath id="clip0_964_1872">
                          <rect
                            width="30"
                            height="30"
                            fill="white"
                            transform="translate(0.5)"
                          />
                        </clipPath>
                      </defs>
                    </svg>
                  </div>
                  <p>Flawless UI/UX</p>
                </div>
                <div className="blockchain_dev_btns">
                  <div className="blockchain_dev_icon">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="31"
                      height="30"
                      viewBox="0 0 31 30"
                      fill="none"
                    >
                      <g clip-path="url(#clip0_964_1872)">
                        <path
                          fill-rule="evenodd"
                          clip-rule="evenodd"
                          d="M0.5 15C0.5 11.0218 2.08035 7.20644 4.8934 4.3934C7.70644 1.58035 11.5218 0 15.5 0C19.4782 0 23.2936 1.58035 26.1066 4.3934C28.9196 7.20644 30.5 11.0218 30.5 15C30.5 18.9782 28.9196 22.7936 26.1066 25.6066C23.2936 28.4196 19.4782 30 15.5 30C11.5218 30 7.70644 28.4196 4.8934 25.6066C2.08035 22.7936 0.5 18.9782 0.5 15ZM14.644 21.42L23.28 10.624L21.72 9.376L14.356 18.578L9.14 14.232L7.86 15.768L14.644 21.422V21.42Z"
                          fill="#7534FC"
                        />
                      </g>
                      <defs>
                        <clipPath id="clip0_964_1872">
                          <rect
                            width="30"
                            height="30"
                            fill="white"
                            transform="translate(0.5)"
                          />
                        </clipPath>
                      </defs>
                    </svg>
                  </div>
                  <p>Flawless UI/UX</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <TalkToUs
          show={talkToUsShow}
          onHide={handleToggleTalkToUs}
          closeButton={true}
          size={"lg"}
        >
          <div class="screen__content">
            <form onSubmit={handleSubmit(handleOnSubmit)} class="login">
              <div class="login__field">
                <i class="login__icon fas fa-user"></i>
                <input
                  type="text"
                  class="login__input"
                  {...register("name")}
                  placeholder="Your Name"
                />
                <p className="text-danger mb-0">{errors.name?.message}</p>
              </div>
              <div class="login__field">
                <i class="login__icon fas fa-lock"></i>
                <input
                  type="email"
                  class="login__input"
                  {...register("email")}
                  placeholder="Email"
                />
                <p className="text-danger mb-0">{errors.email?.message}</p>
              </div>
              <div class="login__field">
                <i class="login__icon fas fa-lock"></i>
                <input
                  type="text"
                  class="login__input"
                  placeholder="Skype / Mobile"
                  {...register("mobile")}
                />
                <p className="text-danger mb-0">{errors.mobile?.message}</p>
              </div>
              <div class="login__field">
                <i class="login__icon fas fa-lock"></i>
                <select class="login__input" {...register("country")}>
                  <option value="">Select</option>
                  {countries.map((ele, ind) => {
                    return (
                      <option key={ind} value={ele.isoCode}>
                        {ele.name}
                      </option>
                    );
                  })}
                </select>
                <p className="text-danger mb-0">{errors.country?.message}</p>
              </div>
              <div class="login__field">
                <i class="login__icon fas fa-lock"></i>
                <textarea
                  id="content"
                  placeholder="Type content here"
                  rows={5}
                  className="login__input"
                  {...register("content")}
                  maxLength={200}
                ></textarea>
                <p className="text-danger mb-0">{errors.content?.message}</p>
              </div>
              <button disabled={loading} class="button login__submit">
                <span class="button__text">
                  {" "}
                  Submit {loading && <Loading />}
                </span>
                <i class="button__icon fas fa-chevron-right"></i>
              </button>
            </form>
          </div>
          <div class="screen__background">
            <span class="screen__background__shape screen__background__shape4"></span>
            <span class="screen__background__shape screen__background__shape3"></span>
            <span class="screen__background__shape screen__background__shape2"></span>
            <span class="screen__background__shape screen__background__shape1"></span>
          </div>
        </TalkToUs>
      </div>
    </div>
  );
};

export default EnterpriseBlockchain;
